.approvecontent {
    &__tittle {
        margin-bottom: 0.33em; }
    &__navigation {
        text-transform: uppercase;
        font-size: 1.17vw;
        font-weight: bold;
        color: #fff;
        font-family: $fontLatos;
        margin-bottom: 1em;
        &-item {
            display: block;
            color: #fff;
            background-color: $companyColor;
            font-weight: bold;
            font-size: 1.17vw;
            border-radius: 60px;
            padding: 1em 5.1em;
            text-decoration: none;
            border: none;
            &:hover {
                text-decoration: none;
                color: #fff; } }
        &-next {
            position: relative;
            &:before {
                content: '';
                position: absolute;
                top: 20%;
                left: 150%;
                transform: translate(-50%, 0);
                width: 1.95vw;
                height: 0.97vw;
                background-repeat: no-repeat;
                background-image: url('../img/arrow.png');
                background-size: 1.95vw 0.97vw; } }
        &-back {
            position: relative;
            &:before {
                content: '';
                position: absolute;
                top: 20%;
                right: 100%;
                transform: translate(-50%, 0);
                width: 1.95vw;
                height: 0.97vw;
                background-repeat: no-repeat;
                background-image: url('../img/left-arrow.svg');
                background-size: 1.95vw 0.87vw; } }
        &__row {
            justify-content: space-between; } } }
.approvetable {
    width: 100%;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    margin-bottom: 1em;
    &__head {
        &-row {
            display: -webkit-flex;
            display: -moz-flex;
            display: -ms-flex;
            display: -o-flex;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: stretch;
            & td {
                width: 16.66%;
                background-color: #dfdfdf;
                color: #989898;
                text-transform: uppercase;
                font-size: 1.04vw;
                font-family: $fontLatos;
                padding: 1em 0;
                &:first-child {
                    border-radius: 50px 0 0 50px;
                    padding-left: 2em; }
                &:last-child {
                    border-radius: 0 50px 50px 0; } } } }
    &__body-row {
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: stretch;
        border-bottom: 2px solid  #dedede;
        padding: 1em 0;
        & td {
            width: 16.66%;
            font-size: 1.17vw;
            font-weight: 400;
            color: #919191;
            font-family: $fontLatos;
            vertical-align: middle;
            &:first-child {
                padding-left: 2em; }
            &:nth-child(2) {
                width: 33.2%; }
            &:last-child {
                width: 33.2%; }
            & input {
                border: 1px solid gray; } } } }
