.header {
    background-color: $companyColor;
    padding: 1.9em 3.75em; }

.home {
    height: 2em;
    width: 2em;
    background-image: url('../img/home.svg');
    background-size: 2em 2em;
    background-position: 50% 50%;
    background-repeat: no-repeat; }

.navigation {
    background-color: transparent;
    margin: 0;
    padding: 0;
    width: 26%;
    &__title {
        display: block;
        font-family: $fontLatos;
        font-size: 2.34vw;
        color: #fff; }
    &__back {
        display: block;
        font-family: $fontLatos;
        font-size: 0.91vw;
        color: #d68dff;
        text-transform: uppercase;
        margin: 0 0 0 1.8vw;
        &:hover {
            text-decoration: none;
            color: #d68dff; }
        & span {
            position: relative;
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: -10%;
                transform: translate(-50%, 0);
                background-repeat: no-repeat;
                background-image: url('../img/left-arrow.svg');
                width: 2.1vw;
                height: 1.1vw;
                background-size: 1.5vw 0.8vw; } } } }

.logo {
    background-color: transparent;
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36%;
    &__title-header {
        font-size: 1.3vw;
        margin: 0;
        color: #fff;
        text-transform: uppercase;
        margin-right: 0.4em; }
    &__img {
        display: block;
        height: 1.82vw;
        width: 2.4vw; } }

.setting {
    width: 26%;
    text-align: right;
    font-family: $fontLatos;
    text-align: right;
    font-size: 0.91vw;
    font-weight: bold;
    text-transform: uppercase;
    color: #d68dff;
    font-family: $fontLatos;
    & a {
        text-decoration: none;
        color: #d68dff;
        position: relative;
        &:hover {
            text-decoration: none;
            color: #d68dff; }
        &:before {
            content: '';
            position: absolute;
            top: -3px;
            left: -10%;
            transform: translate(-50%, 0);
            background-repeat: no-repeat;
            background-image: url('../img/setting.svg');
            width: 1.17vw;
            height: 1.17vw;
            background-size: 1.17vw 1.17vw; } }
    &-access {
        & span {
            position: relative;
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: -0.7vw;
                transform: translate(-50%, 0);
                background-repeat: no-repeat;
                background-image: url('../img/request.svg');
                width: 1.17vw;
                height: 1.17vw;
                background-size: 0.97vw 1.17vw; } } } }
